import axios from 'axios'

const renaApi = axios.create({
  baseURL: 'http://localhost:9090/v1/api'
})
renaApi.defaults.headers.post['Content-Type'] = 'application/json'
renaApi.defaults.timeout = 50000

const tokenApi = axios.create({
  baseURL: 'https://fetch-tokens.vercel.app/api'
})
tokenApi.defaults.headers.post['Content-Type'] = 'application/json'
// tokenApi.defaults.headers.common['Authorization'] = AUTH_TOKEN
tokenApi.defaults.timeout = 50000

const fetchTokens = (payload) => tokenApi.post('/', payload)
const fetchItems = (options) => renaApi.get(`/items?${options}`)
const fetchCollections = (options) => renaApi.get(`/collections?${options}`)
const fetchFactoryCollections = (options) =>
  renaApi.get(`/collections/factory?${options}`)
const postCollection = (payload) => renaApi.post('/collections', payload)

export {
  fetchTokens,
  fetchItems,
  fetchCollections,
  fetchFactoryCollections,
  postCollection
}
