import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import InfiniteScroll from 'react-infinite-scroll-component'
import _ from 'lodash'
import Filter from 'components/Filter'
import Sort from 'components/Sort'
import NFT from 'components/NFTCard'

const Explore = () => {
  const dispatch = useDispatch()
  const items = useSelector((state) => state.items.items)
  const data = items.items || []
  const [page, setPage] = useState(1)
  const [perPage, setPerPage] = useState(6)
  const [counts, setCounts] = useState(3)
  const [payload, setPayload] = useState({
    from: 0,
    count: perPage,
    type: 'all',
    sortby: 'createdAt'
    // filterBy: [],
    // category: null,
    // address: null,
  })

  const handleView = (e) => setCounts(e)

  const handleSort = (e) => {
    const temp = { ...payload, ...e }
    setPayload(temp)
    dispatch({ type: 'GETITEMS_REQUEST', payload: temp })
  }

  const handlePage = () => {
    const current = page + 1
    const temp = { ...payload, from: 3000, count: current * perPage }
    setPage(current)
    setPayload(temp)
    dispatch({ type: 'GETITEMS_REQUEST', payload: temp })
  }

  const handleRefresh = () => {}

  useEffect(() => {
    dispatch({ type: 'GETITEMS_REQUEST', payload })
    dispatch({ type: 'GETCOLLECTIONS_REQUEST', payload: { limit: 10 } })
  }, [])

  return (
    <div className="explore">
      <Filter />
      <div className="nft-content d-flex-1" id="scrollableDiv">
        <div className="sort">
          <span>{items.total} results</span>
          <Sort
            className="ms-auto"
            view={counts}
            onToggle={handleView}
            onSort={handleSort}
          />
        </div>
        <div className="d-flex-1">
          <InfiniteScroll
            dataLength={data.length} //This is important field to render the next data
            next={handlePage}
            hasMore
            loader={<h4 className="text-center">Loading...</h4>}
            endMessage={
              <p style={{ textAlign: 'center' }}>
                <b>Yay! You have seen it all</b>
              </p>
            }
            // below props only if you need pull down functionality
            refreshFunction={handleRefresh}
            pullDownToRefresh
            pullDownToRefreshThreshold={50}
            pullDownToRefreshContent={
              <h3 style={{ textAlign: 'center' }}>
                &#8595; Pull down to refresh
              </h3>
            }
            releaseToRefreshContent={
              <h3 style={{ textAlign: 'center' }}>
                &#8593; Release to refresh
              </h3>
            }
            scrollableTarget="scrollableDiv"
          >
            {_.range(0, Math.ceil(data.length / counts)).map((idx1) => (
              <div className="d-flex" key={idx1}>
                {_.range(0, counts).map((idx2) => {
                  if (counts * idx1 + idx2 >= data.length)
                    return <div className="empty d-flex-1" key={idx2} />
                  else
                    return <NFT data={data[idx1 * counts + idx2]} key={idx2} />
                })}
              </div>
            ))}
          </InfiniteScroll>
        </div>
      </div>
    </div>
  )
}

export default Explore
