import EthereumIcon from 'assets/images/crypto/eth.svg'
import PolygonIcom from 'assets/images/crypto/polygon.svg'

const chains = [
  { name: 'Ethereum', img: EthereumIcon },
  { name: 'Polygon', img: PolygonIcom }
]

function Chains() {
  return (
    <div className="chains">
      {chains.map((chain, index) => (
        <div className="d-flex py-1" key={`ch-${index}`}>
          <img src={chain.img}></img>
          <span className="ms-2">{chain.name}</span>
        </div>
      ))}
    </div>
  )
}

export default Chains
